import React from "react";
import classes from "./SkillsItem.module.css"

const SkillsItem = () => {
  return (
    <div className={`container ${classes.skillsContainer}`} variant="flush">
      <h3>Key Front End Developers Skills</h3>
      <div className={classes.skillsDiv}>
      <p>HTML: (Hyper Text Markup Language)</p>
      <p>CSS: (Cascading Style Sheets)</p>
      <p>Js: (JavaScript)</p>
      </div>
      <h3 className="mt-3">Front End Frameworks</h3>
      <div className={classes.skillsDiv}>
      <p>Bootstrap</p>
      <p>React.js</p>
      <p>Reactstrap</p>
      </div>
      <h3 className="mt-3">Other Skills</h3>
      <div className={classes.skillsDiv}>
      <p>Responsive and Mobile Design</p>
      <p>RESTful Services/APIs</p>
      <p>Cross-Browser Development</p>
      <p>Testing/Debugging</p>
      <p>Git/Version Control</p>
      <p>Visual studio code</p>
      <p>Photoshop</p>
      <p>Problem Solving</p>
      </div>
    </div>
  );
};
export default SkillsItem;
