import React from "react";
import classes from "./ContactForm.module.css";

const ContactForm = () => {


  return (
    <div className={`${classes.contactContainer} container mx-auto my-4`}>
      <form
        action="mailto:narotams97@gmail.com"
        method="post"
        enctype="text/plain"
      >
        <div className="d-flex justify-content-between align-items-center">
          <div className={`${classes.halfField} d-flex flex-column text-start`}>
            <label className="h5 text-secondary">Name</label>
            <input name="name" type="text" required />
          </div>
          <div className={`${classes.halfField} d-flex flex-column text-start`}>
            <label className="h5 text-secondary">Email</label>
            <input name="email" type="email" required />
          </div>
        </div>
        <div className="centered-full-field d-flex flex-column text-start my-3">
          <label className="h5 text-secondary">Message</label>
          <textarea className="message-text" name="message" rows="5"></textarea>
        </div>
        <div>
          <button type="submit" className="btn btn-outline-primary">Submit</button>
        </div>
      </form>
    </div>
  );
};

export default ContactForm;
