import React from "react";
import { Container, Image, Nav, Navbar } from "react-bootstrap";
import { NavLink } from "react-router-dom";

const MainNavigation = () => {
  return (
    <Navbar bg="light" expand="lg" className="shadow-sm rounded-3">
      <Container fluid>
        <NavLink to="/cv/" className='p-0'>
          <Image src="https://raw.githubusercontent.com/narotamji/cv/master/Images/logo.png" className="w-25" alt="logo" />
        </NavLink>
        <Navbar.Toggle aria-controls="basic-navbar-nav" className="" />
        <Navbar.Collapse  className="justify-content-end">
          <Nav className="">
            <NavLink className="nav-link text-center" to="/cv/about">
              About
            </NavLink>
            <NavLink className="nav-link text-center" to="/cv/skills">
              Skills
            </NavLink>
            <NavLink className="nav-link text-center" to="/cv/projects">
              Projects
            </NavLink>
            <NavLink className="nav-link text-center" to="/cv/experience">
              Experience
            </NavLink>
            <NavLink className="nav-link text-center" to="/cv/contact">
              Contact
            </NavLink>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default MainNavigation;
